import axios from "axios";
import { TINY_URL } from '../Api'
const apiToken = `YhhvoiyS1HLShf3CsiZAUsUZK2qTY1JGrJ045Z12sipRKGeEbM1FVHnQVd4f`
export const tinyRequest = (url) => {
    var data = JSON.stringify({
        "url": url
    });
    var config = {
        method: 'post',
        url: `${TINY_URL}?api_token=${apiToken}`,
        headers: {
            'accept': 'application/json',
            'Content-Type': 'application/json'
        },
        data: data
    };
    return axios(config)
}